<template>
  <div class="calendar">
    <vue-cal
      :time="true"
      active-view="week"
      :time-from="18 * 60"
      :time-to="24 * 60"
      :selectedDate="selectedDate"
      :hideWeekdays="showDate"
      :events="calendarEvents"
      :split-days="locationsPerDay"
      :disable-views="['years', 'year', 'month','day']"
      :dragToCreateEvent="false"
      :hideTitleBar="true"
      :hideViewSelector="true"
      :specialHours="specialHours"
      :minDate="minDate"
      :maxDate="maxDate"
      sticky-split-labels
      :timeStep="30"
      :showWeekNumbers="false"
      :timeCellHeight="50"
      :overlaps-per-time-step="true"
      :watchRealTime="true"
      :dblclickToNavigate="false"
      :locale="lang"
    />
  </div>
</template>

<script>
import VueCal from "vue-cal";
import "vue-cal/dist/i18n/es.es.js";
import moment from "moment";

export default {
  props: {
		events: {
			type: Array,
			required: false,
      default: () => []
		},
		locations: {
			type: Array,
			required: false,
      default: () => []
		},
    lang: {
      type: String,
      required: false,
      default: 'es'
    },
    showDate: {
      type: Array,
      required: false,
      default: () => [1,2,3,5,6,7]
    }
	},
  data() {
    return {
      // the min date is August 11nd 2022 and the max date is August 14th 2022
      minDate: "2023-06-15",
      maxDate: "2023-06-15",
      selectedDate: "2023-06-15",
      specialHours: {
        // 1: { from: 2 * 60, to: 2.5 * 60, class: 'final'},
        // 2: { from: 2 * 60, to: 2.5 * 60, class: 'final'},
        // 3: { from: 2 * 60, to: 2.5 * 60, class: 'final'},
        // 4: { from: 2 * 60, to: 2.5 * 60, class: 'final'},
        // 6: { from: 2 * 60, to: 2.5 * 60, class: 'final'},
        // 7: { from: 2 * 60, to: 2.5 * 60, class: 'final'}
      },
			calendarEvents: [],
      locationsPerDay: []
    };
  },
  components: {
    VueCal,
  },
  mounted() {
    this.getLocationsPerDay();
    this.getEvents();
  },
  methods: {
    getEvents() {
			let _auxEvents = []
      let eventsPerLocation = []
      const minDate = moment(this.minDate);
      const maxDate = moment(this.maxDate).endOf('day');

      // get events from each location
      this.locationsPerDay.forEach(location => {
        // make a copy of this.events
        let _eventsCopy = JSON.parse(JSON.stringify(this.events))

        let _events = _eventsCopy.filter(event => event.location === location.id)
        eventsPerLocation.push({
          location: location.id,
          events: _events
        })
      })
      
      // for each location, we need to fill the gaps with empty events
      eventsPerLocation.forEach(location => {
        location.events.forEach(event => {
          event.start_time = moment(event.start_time);
          event.end_time = moment(event.end_time);
        });

        // Sort the events by start time
        location.events.sort((a, b) => a.start_time - b.start_time);

        // Create a new array to hold the expanded events
        const expandedEvents = [];

        // Loop through the events and expand them to fill any gaps
        for (let i = 0; i < location.events.length; i++) {
          // If this is the first event, add a closed event before it if necessary
          if (i === 0 && location.events[i].start_time.isAfter(minDate)) {
            expandedEvents.push({
              "name_es": "Cerrado",
              "name_en": "Closed",
              "start_time": minDate,
              "end_time": location.events[i].start_time,
              "description_es": null,
              "description_en": null,
              "location": location.location,
              "speakers": null,
              "is_nsfw": false,
              "is_streamed": false,
              "is_closed": true,
              "is_unlisted": false,
              "is_canceled": false,
              // "name_en": "Closed",
              // "name_es": "Cerrado",
              // "start_time": minDate,
              // "end_time": location.events[i].start_time,
              // "is_closed": true
            });
          }
          
          // If this is not the first event, add a closed event before it if necessary
          if (i > 0 && location.events[i].start_time.isAfter(location.events[i - 1].end_time)) {
            expandedEvents.push({
              "name_es": "Cerrado",
              "name_en": "Closed",
              "start_time": location.events[i-1].end_time,
              "end_time": location.events[i].start_time,
              "description_es": null,
              "description_en": null,
              "location": location.location,
              "speakers": null,
              "is_nsfw": false,
              "is_streamed": false,
              "is_closed": true,
              "is_unlisted": false,
              "is_canceled": false
              // "name_en": "Closed",
              // "name_es": "Cerrado",
              // "start_time": location.events[i - 1].end_time,
              // "end_time": location.events[i].start_time,
              // "is_closed": true
            });
          }
          
          // Add the original event
          expandedEvents.push(location.events[i]);
          
          // If this is the last event, add a closed event after it if necessary
          if (i === location.events.length - 1 && location.events[i].end_time.isBefore(maxDate)) {
            expandedEvents.push({
              "name_es": "Cerrado",
              "name_en": "Closed",
              "start_time": location.events[i].end_time,
              "end_time": maxDate,
              "description_es": null,
              "description_en": null,
              "location": location.location,
              "speakers": null,
              "is_nsfw": false,
              "is_streamed": false,
              "is_closed": true,
              "is_unlisted": false,
              "is_canceled": false
              // "name_en": "Closed",
              // "name_es": "Cerrado",
              // "start_time": location.events[i].end_time,
              // "end_time": maxDate,
              // "is_closed": true
            });
          }
        }

        // Filter out any events that are outside the minDate and maxDate range
        const filteredEvents = expandedEvents.filter(event => {
          return event.end_time.isAfter(minDate) && event.start_time.isBefore(maxDate);
        });
        
        console.log(filteredEvents)
        location.events = filteredEvents;
        
      })

      // merge all events into one array
      let _mergedEvents = []
      eventsPerLocation.forEach(location => {
        _mergedEvents = _mergedEvents.concat(location.events)
      })

      _mergedEvents.forEach(event => {
        let classList = []
        if(event.is_nsfw) {
          classList.push('nsfw')
        } 
        if(event.is_unlisted) {
          classList.push('unlisted')
        }
        if(event.is_streamed) {
          classList.push('streamed')
        }
        if(event.is_closed) {
          classList.push('is-closed')
        }
        if(this.showDate.length == 6){
          classList.push('bigger')
        }
        let _event = {
					id: event._id,
					start: new Date(event.start_time),
					end: new Date(event.end_time),
          split: event.location,
				}
        if(this.lang === 'en') {
          _event.title = `${event.is_canceled ? '❌ ' : ''}${event.is_streamed ? '🎦 ' : ''}${event.is_nsfw ? '🔞 ' : ''}${event.is_unlisted ? '🔇 ' : ''}${event.name_en}`
        } else {
          _event.title = `${event.is_canceled ? '❌ ' : ''}${event.is_streamed ? '🎦 ' : ''}${event.is_nsfw ? '🔞 ' : ''}${event.is_unlisted ? '🔇 ' : ''}${event.name_es}`
        }
        if(classList.length > 0) {
          _event.class = classList.join(' ')
        }
				_auxEvents.push(_event);
			});
			this.calendarEvents = _auxEvents
    },
    getLocationsPerDay() {
			let _auxLocations = []
      this.locations.forEach(location => {
				_auxLocations.push({
					id: location._id,
					class: this.slugify(location.name),
					label: location.name,
				});
			});
			this.locationsPerDay = _auxLocations
    },
  },
	// watch: {
	// 	events: {
	// 		handler() {
	// 			this.getEvents();
	// 		},
	// 		deep: true
	// 	},
	// 	locations: {
	// 		handler() {
	// 			this.getLocationsPerDay();
	// 		},
	// 		deep: true
	// 	},
  //   lang: {
  //     handler() {
  //       this.getEvents();
  //     },
  //     deep: true
  //   },
  //   showDate: {
  //     handler() {
  //       this.getEvents();
  //     },
  //     deep: true
  //   }
	// }
};
</script>

