<script>
import Calendar from '@/components/cronograma/Calendar.vue' 
import EventDescription from '@/components/cronograma/EventDescription.vue'
import moment from 'moment'

export default {
  data(){
    return {
      isLoading: false,
      componentKey: 0,
      locations: [],
      allEvents: [],
      events: [],
      closedTimes: [],
      lang: 'es',
      showDate: [1,2,3,4],
      showDateSelected: 0,
      selectedLocation: null,
      filterDate: null,
    }
  },
  components: {
    Calendar,
    EventDescription
  },
   mounted() {
    this.getAll()
  },
  computed: {
    eventsByLocation() {
      const eventsFilteredNoPrevia = this.events.filter(e => (moment(e.start_time).format('YYYY-MM-DD') != '2023-06-15' && moment(e.start_time).subtract(1,'minute').format('YYYY-MM-DD') != '2023-06-15'))
      const _eventsFilteredByDate = eventsFilteredNoPrevia.filter(event => {
        if(this.filterDate == null) {
          return true
        }
        return moment(event.start_time).subtract(3,'hour').format('YYYY-MM-DD') == this.filterDate
      })
      if(this.selectedLocation != null) {
        return _eventsFilteredByDate.filter(event => (event.location == this.selectedLocation))
      } else {
        return _eventsFilteredByDate
      }
    },
  },
  methods: {
    selectShowDate(day) {
      this.showDateSelected = day
      switch(day){
        case 0:
          this.showDate = [1,2,3,4]
          break;
        case 1:
          this.showDate = [1,2,3,4,6,7]
          break;
        case 2:
          this.showDate = [1,2,3,4,5,7]
          break;
        case 3:
          this.showDate = [1,2,3,4,5,6]
          break;
        default: 
          this.showDate = [1,2,3,4]
          break;
        }
      this.componentKey = this.componentKey + 1
    },
     getAll() {
      this.isLoading = true
      this.startLoading();
      // dummy data
      Promise.all([
        this.$http.get('/conops/schedule/locations'),
        this.$http.get('/conops/schedule/events')
      ])
      .then(response => {
        this.locations = response[0].data.locations
        // sort event by start_time ASC
        // remove 1 minute from endtime to avoid overlap with starttime
        
        this.allEvents = response[1].data.events.filter(e => (moment(e.start_time).format('YYYY-MM-DD') != '2023-06-15' && moment(e.start_time).subtract(1,'minute').format('YYYY-MM-DD') != '2023-06-15'))
        // this.events = response[1].data.events.filter(e => !e.is_closed).sort((a, b) => {
        //   return moment(a.start_time).diff(moment(b.start_time))
        // })
        if(this.initialized){
          this.componentKey = this.componentKey + 1
        }
        this.initialized = true
      })
      .catch(err => {
        console.log(err)
        this.initialized = false
        this.$buefy.snackbar.open({
          message: 'Error getting data',
        })
      })
      .finally( () => {
        this.stopLoading();
        this.isLoading = false
      })
    },
    // getFormatedDate(date){
    // // format date to YYYY-mm-dd HH:MM:SS
    // return moment(date).format('DD/MM/YYYY')
    // },
    getEventsByDayAndLocation(day) {
      // return events between the day at 04:00:00 to the next day at 04:00:00
      const eventsFilteredNoPrevia = this.allEvents.filter(e => (moment(e.start_time).format('YYYY-MM-DD') != '2023-06-15' && moment(e.start_time).subtract(1,'minute').format('YYYY-MM-DD') != '2023-06-15'))
      const _eventsFilteredByDate = eventsFilteredNoPrevia.filter(event => {
        if(day == null) {
          return true
        }
        return moment(event.start_time).subtract(3,'hour').format('YYYY-MM-DD') == day
      })
      if(this.selectedLocation != null) {
        return _eventsFilteredByDate.filter(event => (event.location == this.selectedLocation))
      } else {
        return _eventsFilteredByDate
      }
    },
  }
}
</script>

