<template>
  <section>
    <div class="has-text-centered mb-5">
        <b-field :label="$t('calendar.calendarLanguage')" centerd>
          <b-switch
            v-model="lang"
            passive-type='is-primary'
            type='is-primary'
            size='is-medium'
            true-value="en"
            false-value="es"
            >
            {{ lang === 'es' ? $t('calendar.calendarLanguage_es') : $t('calendar.calendarLanguage_en') }}
          </b-switch>
        </b-field>
      </div>
    <CalendarPrevia :events="allEvents" :lang="lang" :locations="locations" :showDate="showDate" v-if="initialized"/>
    <div v-if="initialized" class="container">
    <h1 class="title is-3 has-text-centered is-uppercase has-background-black has-text-white py-4 px-2 my-6">{{$t('calendar.listSchedule')}}</h1>
    <div class="columns is-centered">
      <div class="column is-6">
        <h1 class="title is-3 has-text-centered is-uppercase has-background-black has-text-white py-4 px-2">{{$t('calendar.filterPerDay.thursday')}}</h1>
        <EventDescription v-for="event in getEventsByDayAndLocation('2023-06-15')" :event="event" :key="`event-previa-${event._id}`" :lang="lang" :locations="locations"/>
      </div>
    </div>
    </div>
  </section>
</template>

<script>
import CalendarPrevia from '@/components/cronograma/CalendarPrevia.vue' 
import EventDescription from '@/components/cronograma/EventDescription.vue'
import moment from 'moment'

export default {
  data(){
    return {
      isLoading: false,
      componentKey: 0,
      locations: [],
      allEvents: [],
      events: [],
      closedTimes: [],
      lang: 'es',
      showDate: [1,2,3,5,6,7],
      showDateSelected: 0,
      selectedLocation: null,
      filterDate: null,
      initialized: false
    }
  },
  components: {
    CalendarPrevia,
    EventDescription
  },
   mounted() {
    this.getAll()
  },
  computed: {
    eventsByLocation() {
      const eventsFilteredNoPrevia = this.events.filter(e => (moment(e.start_time).format('YYYY-MM-DD') == '2023-06-15' || moment(e.start_time).subtract(1,'hour').format('YYYY-MM-DD') == '2023-06-15'))
      const _eventsFilteredByDate = eventsFilteredNoPrevia.filter(event => {
        if(this.filterDate == null) {
          return true
        }
        return moment(event.start_time).subtract(3,'hour').format('YYYY-MM-DD') == this.filterDate
      })
      if(this.selectedLocation != null) {
        return _eventsFilteredByDate.filter(event => (event.location == this.selectedLocation))
      } else {
        return _eventsFilteredByDate
      }
    },
  },
  methods: {
     getAll() {
      this.isLoading = true
      this.startLoading();

      // dummy data
      Promise.all([
        this.$http.get('/conops/schedule/locations'),
        this.$http.get('/conops/schedule/events')
      ])
      .then(response => {
        this.locations = response[0].data.locations
        // sort event by start_time ASC
        // remove 1 minute from endtime to avoid overlap with starttime
        
        this.allEvents = response[1].data.events.filter(e => (moment(e.start_time).format('YYYY-MM-DD') == '2023-06-15' || moment(e.start_time).subtract(1,'minute').format('YYYY-MM-DD') != '2023-06-15'))
        // this.events = response[1].data.events.filter(e => !e.is_closed).sort((a, b) => {
        //   return moment(a.start_time).diff(moment(b.start_time))
        // })
        // // filter events from day 2023-06-15
        // this.closedTimes = response[1].data.events.filter(e => e.is_closed).sort((a, b) => {
        //   return moment(a.start_time).diff(moment(b.start_time))
        // })
        if(this.initialized){
          this.componentKey = this.componentKey + 1
        }
        this.initialized = true
      })
      .catch(err => {
        console.log(err)
        this.initialized = false
        this.$buefy.snackbar.open({
          message: 'Error getting data',
        })
      })
      .finally( () => {
        this.stopLoading();
        this.isLoading = false
      })
    },
    getEventsByDayAndLocation(day) {
      // return events between the day at 04:00:00 to the next day at 04:00:00
      const _eventsFilteredByDate = this.allEvents.filter(event => {
        if(day == null) {
          return true
        }
        return moment(event.start_time).subtract(3,'hour').format('YYYY-MM-DD') == day
      })
      if(this.selectedLocation != null) {
        return _eventsFilteredByDate.filter(event => (event.location == this.selectedLocation))
      } else {
        return _eventsFilteredByDate
      }
    },
  }
}
</script>

